import {Directive, ElementRef, Input, OnInit} from '@angular/core';
declare var $: any;

@Directive({
  selector: '[appCollapse]'
})
export class CollapseDirective implements OnInit {
  @Input() expanded: boolean = false;
  @Input() toggleEl: HTMLElement;

  private _element;

  constructor(
    private _el: ElementRef
  ) { }

  ngOnInit() {
    this._element = $(this._el.nativeElement);

    // Click toggle
    if (this.toggleEl) {
      this.toggleEl.addEventListener('click', this.toggle.bind(this));
    }

    this.expanded = !this.expanded;
    this.toggle();

  }

  show() {
    this.expanded = true;
    this._element.collapse('show').parent().addClass('collapse-expanded');
  }

  hide() {
    this.expanded = false;
    this._element.collapse('hide').parent().removeClass('collapse-expanded');
  }

  toggle() {
    if (this.expanded) {
      this.hide();
    } else {
      this.show();
    }
  }

}
