import {Directive, ElementRef, EventEmitter, HostListener, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {AutocompleteListDirective} from "./autocomplete-list.directive";

@Directive({
  selector: '[appAutocompleteInput]'
})
export class AutocompleteInputDirective implements OnInit, OnDestroy {

  @Input() autoList: AutocompleteListDirective;
  @Output() onBackSpaceRemove = new EventEmitter();

  @HostListener('focus') onFocus() {
    this.autoList.open();
  }

  @HostListener('keydown', ['$event']) onInput(e) {
    switch (e.which) {
      case 8:
        if (!e.target.value) {
          this.onBackSpaceRemove.emit(e);
        }
        break;
    }
    this.autoList.onInput(e);
  }

  constructor(
    protected el: ElementRef
  ) { }

  ngOnInit() {
    this.hideList = this.hideList.bind(this);
    // Click out hide list
    document.addEventListener('click', this.hideList);
  }

  ngOnDestroy() {
    document.removeEventListener('click', this.hideList);
  }

  private hideList(e) {
    if (!e.target.isSameNode(this.el.nativeElement)) {
      this.autoList.hide();
    }
  }
}
