import {Injectable} from '@angular/core';

@Injectable()
export class NumberUtilsService {

    isNumeric(value):boolean {
        if ((undefined === value) || (null === value)) {
            return false;
        }
        if (typeof value === 'number') {
            return true;
        }
        if (!/^[0-9.,]+$/.test(value)) {
            return false;
        }
        if (!/^(?=.*\d)\d*[\.\,]?\d*$/.test(value)) {
            return false;
        }
        return !isNaN(parseFloat(value) - 0);
    }

    isObjectEmpty(obj): boolean {
        return Object.keys(obj).length === 0 && obj.constructor === Object;
    }

    addSeparator(value, separator: string = ','): string {
        if (value && separator) {
            value = value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, separator);
        }
        return value;
    }

    removeSeparator(value, separator: string = ','): string {
        if (value && typeof value !== 'number') {
            switch (separator) {
                case ' ':
                    value = value.toString().replace(/[\s]/g, '');
                    break;
                case ',':
                    value = value.toString().replace(/\,/g, '');
                    break;
                default:
                    value = value.toString().replace(/separator/g, '');
                    break;
            }
        }
        return value;
    }

}
