import {Injectable} from '@angular/core';
import {HttpClientService} from "inet-core";
import {Observable} from "rxjs";
declare let iNet: any;
@Injectable({
    providedIn: 'root'
})
export class SharingInformationService {
    private url = {
        application_list: iNet.getPUrl("system/application/list"), //firmContext (appname)
        data_rights: iNet.getPUrl("datarights/available"), //params: appname
        user_rights_list: iNet.getPUrl("datarights/list"), //params: appname, group, myshare: boolean
        data_rights_update: iNet.getPUrl("datarights/update"), //params: {member: string, group: string, right: string},
        data_rights_delete: iNet.getPUrl("datarights/delete"), //params: appname
        users_suggest: iNet.getPUrl("cloud/profileacct/list")
    };

    constructor(private http: HttpClientService) {
    }


    getApplicationList(params: any): Observable<any> {
        this.http.showLoading();
         return this.http.getJSON(this.url.application_list, params)
    }

    getApplicationRights(params: any): Observable<any>{
        this.http.showLoading();
        return this.http.getJSON(this.url.data_rights, params)
    }

    getUserRights(params: any): Observable<any>{
        this.http.showLoading();
        return this.http.getJSON(this.url.user_rights_list, params)
    }

    updateUserRight(params: any):Observable<any>{
        this.http.showLoading();
        return this.http.postJSON(this.url.data_rights_update, params)
    }

    deleteUserRight(params: any): Observable<any>{
        this.http.showLoading();
        return this.http.postJSON(this.url.data_rights_delete, params)
    }

    getSuggestUser(params: any): Observable<any>{
        return this.http.postJSON(this.url.users_suggest, params)
    }
}
