import {
    Component,
    ContentChild, ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
    TemplateRef
} from '@angular/core';

@Component({
    selector: 'app-custom-pagination',
    templateUrl: './custom-pagination.component.html',
    styleUrls: ['./custom-pagination.component.css']
})

export class CustomPaginationComponent implements OnInit, OnChanges {
    @Input("pageSize") pageSize = 10;
    @Input("pageNumber") pageNumber = 1;
    @Input("totalItems") totalItems = 0;
    @Input("firstLoad") firstLoad = true;
    @Input("params") params = {};
    @ContentChild('basicSearch') basicSearch: TemplateRef<ElementRef>;
    @ContentChild('advanceSearch') advanceSearch: TemplateRef<ElementRef>;
    query: any;
    pageCount = 0;
    @Output() onLoad = new EventEmitter<any>();

    constructor() {

    }

    ngOnChanges(changes: SimpleChanges) {
        this.processOnTotalChanged();
    }

    ngOnInit() {
        if (this.firstLoad) {
            this.load();
        }
    }

    changeToFirstPage(event: any) {
        this.pageNumber = 1;
        this.onPageChanged(event, this.pageNumber);
    }

    changeToLastPage(event: any) {
        this.pageNumber = this.pageCount;
        this.onPageChanged(event, this.pageNumber);
    }

    changeToNextPage(event: any) {
        if (this.pageNumber < this.pageCount) {
            this.pageNumber++;
            this.onPageChanged(event, this.pageNumber);
        }
    }

    changeToPreviousPage(event: any) {
        if (this.pageNumber > 1) {
            this.pageNumber--;
            this.onPageChanged(event, this.pageNumber);
        }
    }

    changeToCurrentPage(event: any) {
        this.pageNumber = event.currentTarget.value;
        if (this.pageNumber < 1) {
            this.pageNumber = 1;
        } else if (this.pageNumber > this.pageCount) {
            this.pageNumber = this.pageCount;
        }
        event.currentTarget.value = this.pageNumber;

        this.onPageChanged(event, this.pageNumber);
    }

    get maxPage() {
        return Math.ceil(this.totalItems / this.pageSize);
    }

    getQuery(): any {
        let query = {
            pageNumber: (this.pageNumber < 1) ? 0 : this.pageNumber - 1,
            pageSize: this.pageSize
        };
        query = {...this.getParams(), ...query};
        return query;
    }

    setTotalItems(v: number) {
        this.totalItems = v;
        this.processOnTotalChanged();
    }

    setParams(params: any = {}) {
        this.params = params;
    }

    getParams(): any {
        return this.params || {};
    }

    reload(event?: any) {
        this.onLoad.emit(this.getQuery());
    }

    load() {
        let query = {newPage: 1, pageSize: this.pageSize};
        query = {...this.getParams(), ...query};
        this.processOnPaginationChanged(null, query);
    }

    onPageChanged(event?: Event, pageNumber?: number) {
        this.processOnPaginationChanged(event, {newPage: pageNumber, pageSize: -1});
    }

    processOnPaginationChanged(event: Event, args: any): string {
        this.pageNumber = args.newPage;
        this.onLoad.emit(this.getQuery());
        return 'onPaginationChanged';
    }

    processOnTotalChanged() {
        this.pageCount = this.maxPage || 0;
        this.pageNumber = this.pageCount > 0 ? 1 : 0;
        this.query = this.getQuery();
    }
}
