import {
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewContainerRef
} from '@angular/core';
import {NavigationEnd, Router} from "@angular/router";
import {filter} from "rxjs/operators";

@Component({
  selector: '[navigationTab]',
  templateUrl: './navigation-tab.component.html',
  styleUrls: ['./navigation-tab.component.css']
})
export class NavigationTab implements OnInit, OnDestroy {
  @Input() tabs: NavigationData[];
  @Input() viewContainerRef: ViewContainerRef;
  @Input() prefix: string = '';

  @Output() onChange = new EventEmitter<any>();
  @Output() onLoad = new EventEmitter<any>();

  tabActive: NavigationData;
  componentRef: ComponentRef<any>;

  private _router;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private router: Router
  ) { }

  ngOnInit() {

    this._router = this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe((event: NavigationEnd) => {
        this._activeTab();
      });

    this._activeTab();
  }

  ngOnDestroy() {
    if (this._router) {
      this._router.unsubscribe();
    }
  }

  activeTab(tab: NavigationData) {
    if (tab.active) {
      return;
    }
    this.router.navigate([this.getFullPath(tab)]);
    this.onChange.emit(tab);
  }

  getTabByPath(path: string) {

    // get tab with path matching
    for (let i = 0; i < this.tabs.length; i++) {
      if (this.getFullPath(this.tabs[i]) === path) {
        return this.tabs[i];
      }
    }

    return this.tabs[0];
  }

  getFullPath(tab: NavigationData) {
    return this.prefix + tab.path;
  }

  private _loadComponent(tab: NavigationData) {
    if (this.tabActive === tab) {
      return;
    }
    this.tabActive = tab;
    this.viewContainerRef.clear();
    if (tab.component) {
      let componentFactory = this.componentFactoryResolver.resolveComponentFactory(tab.component);
      this.componentRef = this.viewContainerRef.createComponent(componentFactory);
      this.onLoad.emit(this.componentRef);
    }
  }

  private _activeTab() {
    let tab = this.getTabByPath(this.router.url);
    if (tab) {
      this.tabs.forEach(_tab => _tab.active = false);
      this._loadComponent(tab);
      tab.active = true;
    }
  }
}

export interface NavigationData {
  name: string
  component: any
  path: string
  active?: boolean
}
