export class NotifyMessage {
  uuid: string;
  application: string;
  context: string;
  activityID: string;
  message: string;
  sender: string;
  fullname: string;
  usercode: string;
  created: number;
  read: boolean;
  mredirectURL: string;
  mshortcutURL: string;
  redirectURL: string;
  shortcutURL: string;
  params: any
}

export class NotifyMessageContent {
  action: string;
  alert: string;
  badge: string;
  content: string;
  msgID: string;
  sound: string = 'default';
  ttl: string;
}

export class NotifyMessageItem {
  uuid: string;
  alias: string[];
  application: string;
  channelIds:  string[];
  deviceTypes: string[];
  message: NotifyMessageContent;
  messageType: string;
  organization: string;
  sender: string;
  sent: number;
  variants: any[];
}
