import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
    selector: 'app-pagination-list',
    templateUrl: './pagination-list.component.html',
    styleUrls: ['./pagination-list.component.scss']
})
export class PaginationListComponent implements OnInit {

    @Input() total: number;
    @Input() page: number;
    @Input() items;
    @Input() limit: number;
    @Output() changePage = new EventEmitter();
    @Output() load = new EventEmitter();
    itemsPage: Array<any> = [];
    startNumber = 0;
    endNumber = 0;
    totalPage = 0;

    constructor() {
    }

    ngOnInit() {
    }

    loadPagination() {
        setTimeout(() => {
            this.getItemsPage();
        }, 50);
    }

    getItemsPage() {
        if (this.total === 0) {
            this.startNumber = 0;
            this.endNumber = 0;
            this.page = 0;
            this.itemsPage = [];
        }
        else {
            if (this.page === 0) {
                this.page++;
            }
            let totalPage = this.page * this.limit;
            this.startNumber = totalPage - this.limit + 1;
            this.endNumber = totalPage > this.total ? this.total : (this.startNumber + this.limit - 1);
            if (this.page === 1)
                this.itemsPage = this.items.slice(0, totalPage > this.total ? this.total : (this.startNumber + this.limit - 1));
            else {
                this.itemsPage = this.items.slice(this.startNumber - 1, this.endNumber);
            }
        }
        this.totalPage = Math.ceil(this.total / this.limit);
        this.changePage.emit({
            page: this.page,
            items: this.itemsPage
        });
    }

    prevPage() {
        if (this.page > 1) {
            this.page--;
            this.getItemsPage();
        }
    }

    prevFirstPage() {
        if (this.page !== 1) {
            this.page = 1;
            this.getItemsPage();
        }
    }

    nextPage() {
        let maxPage = Math.ceil(this.total / this.limit);
        if (this.page < maxPage) {
            this.page++;
            this.getItemsPage();
        }
    }

    nextLastPage() {
        let maxPage = Math.ceil(this.total / this.limit);
        if (this.page !== maxPage) {
            this.page = maxPage;
            this.getItemsPage();
        }
    }

    reload() {
        this.load.emit({
            page: this.page,
            items: this.getItemsPage()
        });
    }

    inputPageText() {
        let total = Math.ceil(this.total / this.limit);
        if (this.page < 1) {
            this.page = 1;
        }
        else if (this.page > total) {
            this.page = total;
        }
        this.getItemsPage();
    }
}
