import {Component, ElementRef, EventEmitter, Input, Output, ViewChild} from '@angular/core';
import {ModalSelectComponent} from "./modal-select/modal-select.component";
import {CoreService} from "inet-core";
declare let iNet: any;

@Component({
  selector: 'app-select-user',
  templateUrl: './select-user.component.html',
  styleUrls: [
      '../scss/autocomplete.css',
      './select-user.component.css'
  ]
})
export class SelectUserComponent{
  
  @Input('id') id: string = iNet.generateId();
  @Input('selectUsers') selectUsers: any[] = [];
  @Input('isSingle') isSingle: boolean;
  @Input('isValid') isValid: boolean;

  @Output() onAdd = new EventEmitter<any>();

  @ViewChild(ModalSelectComponent) modalSelect: ModalSelectComponent;
  @ViewChild('inputUser') inputUser: ElementRef;
  
  isMouseEnter:boolean = false;
  isFocus:boolean = false;
  users: any[] = [];
  private timer: any;
  private searchValue: string;

  constructor(private coreService: CoreService) { }

  valid(){
    return this.isValid && this.selectUsers.length > 0;
  }

  clearValid(){
    this.isFocus = false;
  }

  validInput(){
    setTimeout(() => {
      if(this.valid()){
        this.isFocus = false;
      }
      else{
        this.isFocus = true;
      }
    }, 250);
  }

  searchUser() {
    clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      let value = this.getKeyword();
      if (value === this.searchValue) {
        return;
      }
      if (!value) {
        this.users.length = 0;
        this.searchValue = value;
        return;
      }
      this.coreService.searchFirmAccount({keyword: value, pageSize: 10}, (data) => {
        let users = data && data['items'] || [];
        this.setUser(users);
      });
    },300);
  }

  removeUser(item: any){
    for(let i = 0; i < this.selectUsers.length; i++){
      if(this.selectUsers[i].username === item.username){
        this.selectUsers.splice(i, 1);
        this.validInput();
        this.onAdd.emit(item);
        return;
      }
    }
  }

  private setDefault(){
    this.inputUser.nativeElement.value = '';
    this.searchValue = '';
    this.users = [];
    this.selectUsers = [];
  }

  addUser(item: any){
    if(this.isSingle){
      this.selectUsers[0] = item;
    }
    else{
      for(let selectMember of this.selectUsers){
        if(selectMember.username === item.username){
          return;
        }
      }
      this.selectUsers.push(item);
    }
    this.inputUser.nativeElement.value = '';
    this.searchValue = '';
    this.focusInput();
    this.validInput();
  }

  clickUser(e, el) {
    if (e.target.isSameNode(el)) {
      this.focusInput();
    }
  }

  removeLastUser() {
    this.selectUsers.splice(-1);
  }

  openModal(){
    this.modalSelect.show();
  }

  private hasUser(user): boolean {
    for (let selectMember of this.selectUsers) {
      if (selectMember.username === user.username) {
        return true;
      }
    }
    return false;
  }

  private setUser(users: any[]) {
    this.users = users.filter((item) => !this.hasUser(item));
  }

  private focusInput() {
    this.inputUser.nativeElement.focus();
  }

  private getKeyword() {
    return this.inputUser.nativeElement.value.trim();
  }

  onMouseEnter(){
    this.isMouseEnter = true;
  }

  onMouseDown(){
    this.isMouseEnter = false;
  }
}
