export class CertUtils {

    static parseCertString(subject: string) {
        const data: any = {};
        subject.split(',').forEach((str) => {
            const values = str.trim().split('=');
            if (data[values[0]]) {
                data[values[0]] += ', ' + values[1];
            } else {
                data[values[0]] = values[1];
            }
        });
        return data;
    }
}