import {
    Component,
    OnInit,
    EventEmitter,
    Output,
    OnDestroy,
    TemplateRef,
    ElementRef,
    Input, ViewChild
} from '@angular/core';
import {CoreService, UserProfileService} from 'inet-core';
import {TranslateService} from "@ngx-translate/core";
import {OrganizationService} from "../../../organization/organization.service";
import {Router} from "@angular/router";
import {CloudTranslateService} from "../../../translate/cloud-translate.service";
import {NotifyMessageService} from "../../../common/notify-message.service";

declare let iNet: any;

@Component({
    selector: 'app-admin-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.css']
})
export class AdminNavbarComponent implements OnInit, OnDestroy {

    @Input() navbarItemTpl: TemplateRef<ElementRef>;
    @Input() profileItemTpl: TemplateRef<ElementRef>;
    @Input() navbarMenuTpl: TemplateRef<ElementRef>;
    @Input() hideToggler: boolean = false;
    @Input() hideTogglerButton: boolean = false;
    @Input() hideCompanyMenu: boolean = false;
    @Input() hideProfileMenu: boolean = false;
    @Input() hideSearch: boolean = false;
    @Input() applicationName: string;
    @Input() homeRouterLink: string;
    @Input() noteRouterLink: string;
    @Input() messageCount: string = '';
    @Input() hideBrandName: boolean = true;
    @Input() hideLogo: boolean = false;
    @Output() onToggleMenu = new EventEmitter<void>();
    @Output() onToggleMessageSideNav = new EventEmitter<void>();
    @Output() onToggleAppSideNav = new EventEmitter<void>();
    // @Output() onToggleChatSideNav = new EventEmitter<void>();
    @Output() onToggleSocialSideNav = new EventEmitter<void>();
    @Output() onToggleNoteSideNav = new EventEmitter<void>();
    @Output() onClickLogo = new EventEmitter<string>();
    @Input() visibleChatIcon: boolean = !iNet.isEmpty(iNet.prefix);
    @Input() visibleMessageIcon: boolean = !iNet.isEmpty(iNet.prefix);
    @Input() visibleAppIcon: boolean = !iNet.isEmpty(iNet.prefix);
    @Input() visibleNoteIcon: boolean = false;
    //externalUrl = 'javascript:;';
    avatarUrl: string = iNet.BLANK_IMAGE_URL;
    displayName = iNet.displayName;
    @Input() brandName: string =  iNet.orgName || '';

    @ViewChild('brandLink') brandLink: ElementRef;
    langs: Array<string> = [];
    currentLanguage: string;

    logoUrl: string;

    constructor(private coreService: CoreService,
                private router: Router,
                private userProfileService: UserProfileService,
                private orgService: OrganizationService,
                private cloudTranslateService: CloudTranslateService,
                private messageService: NotifyMessageService,
                public translate: TranslateService) {
    }

    ngOnInit() {
        //this.brandName = !iNet.isEmpty(this.applicationName) ? `${this.applicationName} - ${this.orgName}` : this.orgName;
        //this.externalUrl = window.location.protocol + '\/\/' + window.location.host + '\/' + iNet.firmPrefix;
        this.langs = this.translate.getLangs(); // Languages
        this.currentLanguage = this.cloudTranslateService.getCurrentLang();
        if(!iNet.isEmpty(iNet.prefix)) {
            this.userProfileService.getFullName().then(fullname => {
                this.displayName = fullname || iNet.displayName || iNet.username;
            });

            this.userProfileService.getAvatarUrl().then(url => {
                this.avatarUrl = url;
            });
            this.countMessage();
            //Load logo for header

            this.logoUrl = this.orgService.getLogoUrl();
        }
    }

    ngOnDestroy() {
    }

    logout() {
        if (this.coreService.getEnvironment()['production']) {
            this.coreService.logout();
        }
    }

    toggleMenu() {
        this.onToggleMenu.emit();
    }

    toggleMessageSideNav($event) {
        this.onToggleMessageSideNav.emit();
    }

    toggleAppSideNav($event) {
        this.onToggleAppSideNav.emit();
    }

    // toggleSocialSideNav($event) {
    //     this.onToggleSocialSideNav.emit();
    // }

    // toggleChatSideNav($event) {
    //     this.onToggleChatSideNav.emit();
    // }

    toggleNoteNav($event) {
        this.onToggleNoteSideNav.emit()
    }

    changeLanguage(lang: string) {
        this.translate.use(lang).subscribe(
            (v: any) => {
                this.currentLanguage = lang;
                this.cloudTranslateService.setCurrentLang(lang);
                if (this.coreService.getEnvironment()['production']) {
                    this.coreService.updateLanguage(lang, function () {
                        window.location.reload();
                    });
                } else {
                    window.location.reload();
                }
            }
        );
    }

    updateImageUrl($event) {
        this.avatarUrl = UserProfileService.DEFAULT_AVATAR_URL;
    }

    updateBlankImage($event) {
        if ($event) {
            this.logoUrl = iNet.BLANK_IMAGE_URL;
        }
    }

    countMessage(){
        this.messageService.count().then((count: number) => {
            this.messageCount = (count && count > 0) ? count.toString() : '';
        })
    }

    setMessageCount(v: string) {
        this.messageCount = v;
    }
    clickLogo($event) {
        this.onClickLogo.emit(this.homeRouterLink);
    }
}
