import {AfterViewInit, Component, ElementRef, EventEmitter, Output, ViewChild} from '@angular/core';
import {FrontViewDirective} from "../front-view.directive";

@Component({
  templateUrl: './viewer.component.html',
  styleUrls: [
    '../../scss/front-view.css'
  ]
})
export class FrontViewComponent implements AfterViewInit {
  @Output() onDestroy = new EventEmitter<any>();
  @ViewChild('viewer') viewer: FrontViewDirective;
  @ViewChild('iframe') iframe: ElementRef;

  viewUrl: string;

  constructor() { }

  ngAfterViewInit() {
    this.viewer.show();
  }

  setViewUrl(viewUrl) {
    this.viewUrl = viewUrl;
    this.iframe.nativeElement.src = viewUrl;
  }

  onHide() {
    this.onDestroy.emit();
  }
}
