import {MessageSignHash} from "./model/MessageSignHash";
import {EXT_ACTION, EXT_CHROME_URL, EXT_FIREFOX_URL, EXT_HASH_ALG, NATIVE_APP_URL} from "./Constants";
import {CertData} from "./model/CertData";
import {ReplyData} from "./model/ReplyData";
import {CertUtils} from "./CertUtils";

export class WebTokenSigner {
    isChrome: boolean = /Google Inc/.test(navigator.vendor);
    isFirefox: boolean = /Firefox/.test(navigator.userAgent);
    isWindows: boolean = navigator.platform.indexOf('Win') > -1;

    osSupported: boolean = this.isWindows;
    browserSupported: boolean = this.isChrome || this.isFirefox;
    appInstalled: boolean = false;
    get extInstalled(): boolean {
        return !!window['_ExtTokenBridge'];
    };

    appInfo = {
        version: null,
        nativeVersion: null,
        nativeLatestVersion: null
    };

    textOsUnSupport: string = 'TÃ­nh nÄng Äang há» trá»£ cho mÃ¡y tÃ­nh dÃ¹ng Windows';
    textBrowserUnSupport: string = 'TÃ­nh nÄng Äang há» trá»£ cho Chrome vÃ  FireFox';
    textExtNotInstalled: string = `
    Cáº§n cÃ i Äáº·t extension <a href="${this.isChrome ? EXT_CHROME_URL : EXT_FIREFOX_URL}" target="_blank">TokenSigner</a> trÃªn trÃ¬nh duyá»t Web
    `;
    textAppNotInstalled: string = `
    Cáº§n cÃ i Äáº·t á»©ng dá»¥ng kÃ½ <a href="${NATIVE_APP_URL}" target="_blank">TokenSigner</a> trÃªn mÃ¡y tÃ­nh
    `;

    title: string = "Chá»©ng thÆ° sá»";
    description: string = "Chá»n chá»©ng thÆ° sá» Äá» kÃ½";
    textOk: string = 'Äá»ng Ã½';
    textCancel: string = 'ÄÃ³ng';
    certNotFound: string = 'KhÃ´ng tÃ¬m tháº¥y chá»©ng thÆ° sá»';
    textIssuer: string = 'NhÃ  cung cáº¥p';
    textValid: string = 'Hiá»u lá»±c';
    zIndex: number = 1100;

    private _dialog;
    private _certEl;
    private _certs: CertData[];
    private _certCallback: Function;

    constructor() {
        this._onEscapeKey = this._onEscapeKey.bind(this);
    }

    checkReady(silent?: boolean): Promise<boolean> {
        return new Promise(resolve => {
            if (this.appInstalled) {
                return resolve(true);
            }

            if (!this.osSupported) {
                if (!silent) {
                    this._createCertDialog(null, this.textOsUnSupport);
                }
                return resolve(false);
            }

            if (!this.browserSupported) {
                if (!silent) {
                    this._createCertDialog(null, this.textBrowserUnSupport);
                }
                return resolve(false);
            }

            if (!this.extInstalled) {
                if (!silent) {
                    this._createCertDialog(null, this.textExtNotInstalled);
                }
                return resolve(false);
            }

            this._loadAppInfo().then(() => {
                if (!this.appInstalled && !silent) {
                    this._createCertDialog(null, this.textAppNotInstalled);
                }
                resolve(this.appInstalled);
            });
        });
    }

    checkNativeUpdate() {
        return this.appInstalled && this.appInfo.nativeVersion < this.appInfo.nativeLatestVersion;
    }

    loadCerts(): Promise<CertData[]> {
        return new Promise(resolve => {
            this.checkReady(true).then((ready) => {
                if (ready) {
                    this._postExt(EXT_ACTION.GET_CERTS).then((data) => {
                        const certs = data && data.data || [];
                        certs.forEach(cert => {
                            cert.subjectData = CertUtils.parseCertString(cert.subject);
                            cert.issuerData = CertUtils.parseCertString(cert.issuer);
                        });
                        resolve(certs);
                    });
                } else {
                    resolve([]);
                }
            });
        });
    }

    signHash(data: MessageSignHash): Promise<ReplyData> {
        return new Promise(resolve => {
            if (data && (data.hash || data.hashs)) {
                this.checkReady().then((ready) => {
                    if (ready) {
                        data.hashAlg = data.hashAlg || EXT_HASH_ALG.SHA1;
                        this._postExt(EXT_ACTION.SIGN_HASH, data).then(resolve);
                    } else {
                        resolve(null);
                    }
                });
            } else {
                resolve({
                    error: "HASH_MISSING"
                });
            }
        });
    }

    selectCert(forceSelect?: boolean) {
        return new Promise(resolve => {
            this.checkReady().then((ready) => {
                if (ready) {
                    this.loadCerts().then((certs) => {
                        if (certs.length > 1 || forceSelect) {
                            this._certCallback = resolve;
                            this._createCertDialog(certs);
                        } else {
                            resolve(certs[0]);
                        }
                    });
                } else {
                    resolve(null);
                }
            });
        });
    }

    dateDisplay(dateStr: string) {
        const date = new Date(dateStr);
        return [date.getDate(), date.getMonth() + 1, date.getFullYear()].join('/');
    }

    private _postExt(action: EXT_ACTION, data?: any): Promise<ReplyData> {
        return new Promise(resolve => {
            if (this.extInstalled) {
                window["_ExtTokenBridge"].postToExtension(action, data).then((response) => {
                    resolve(response);
                });
            } else {
                resolve({
                    error: "EXTENSION_NOT_INSTALL"
                });
            }
        });
    }

    private _loadAppInfo(): Promise<any> {
        return new Promise(resolve => {
            this._postExt(EXT_ACTION.GET_INFO).then((reply: ReplyData) => {
                if (reply && reply.data) {
                    Object.assign(this.appInfo, reply.data);
                }
                this.appInstalled = !!this.appInfo.nativeVersion;
                resolve();
            });
        });
    }

    private _createCertDialog(certs: CertData[], message?: string) {
        if (this._dialog) {
            return;
        }
        this._dialog = document.createElement('div');
        this._dialog.classList.add('webtoken-dialog');
        this._dialog.style.zIndex = this.zIndex;

        this._certs = certs;

        let msg;

        if (message) {
            msg = '<div class="webtoken-dialog__message">' + message + '</div>';
        } else {
            msg = certs.length < 1 ? `<div class="webtoken-dialog__message">${this.certNotFound}</div>` : this._buildCertHtml(certs);
        }

        this._dialog.innerHTML = `
        <style>
        .webtoken-dialog {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: rgba(0, 0, 0, .3);
        }
        .webtoken-dialog__container {
            width: 400px;
            margin: auto;
            background: #fff;
            overflow: hidden;
            color: #333;
            position: absolute;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 50%;
            border-radius: 4px;
        }
        .webtoken-dialog__header {
            padding: 10px 15px;
            border-bottom: 1px solid #ddd;
        }
        .webtoken-dialog__title {
            font-size: 20px;
            font-weight: bold;
            margin-bottom: 5px;
        }
        .webtoken-dialog__desc {
            font-size: 14px;
            line-height: 20px;
        }
        .webtoken-dialog__close {
            float: right;
            width: 30px;
            cursor: pointer;
            text-align: center;
            line-height: 30px;
            margin: -5px 0;
            font-size: 20px;
        }
        .webtoken-dialog__body {
            max-height: 250px;
            overflow: auto;
            font-size: 14px;
            line-height: 20px;
        }
        .webtoken-dialog__message {
            padding: 20px;            
        }
        .webtoken-cert__item {
            padding: 10px 20px;
            cursor: pointer;
            line-height: 20px;
        }
        .webtoken-cert__item.active {
            background: #ddd !important;
        }
        .webtoken-cert__item:hover {
            background: #f5f5f5;
        }
        .webtoken-cert__title {
            font-size: 18px;
            margin-bottom: 2px;
        }
        .webtoken-cert__text {
            font-size: 14px;
        }
        .webtoken-dialog__footer {
            padding: 10px 20px;
            border-top: 1px solid #ddd;
            text-align: right;
        }
        .webtoken-dialog__button {
            height: 30px;
            line-height: 30px;
            padding: 0 10px;
            min-width: 60px;
            margin-left: 10px;
            cursor: pointer;
            border-radius: 4px;
            font-weight: bold;
            background: #fff;
            border: 1px solid #ddd;
        }
        .webtoken-dialog__button:hover {
            opacity: 8;
        }
        .btn-ok {
            background: #007bff;
            border-color: #007bff;
            color: #fff;
        }
        </style>
        <div class="webtoken-dialog__container">
        <div class="webtoken-dialog__header">
        <div class="webtoken-dialog__close">Ã</div>
        <div class="webtoken-dialog__title">${this.title}</div>
        ${message ? '' : `<div class="webtoken-dialog__desc">${this.description}</div>`}
        </div>
        <div class="webtoken-dialog__body">${msg}</div>
        <div class="webtoken-dialog__footer">
        <button class="btn-close webtoken-dialog__button">${this.textCancel}</button>
        ${message ? '' : `<button class="btn-ok webtoken-dialog__button">${this.textOk}</button>`}
        </div>
        </div>
        `;

        const certEl = this._dialog.getElementsByClassName('webtoken-cert__item');

        // Close dialog
        this._dialog.addEventListener('click', () => this._closeDialog());
        this._dialog.getElementsByClassName('webtoken-dialog__close')[0].addEventListener('click', () => this._closeDialog());

        // Prevent close dialog
        this._dialog.getElementsByClassName('webtoken-dialog__container')[0].addEventListener('click', function (e) {
            e.stopPropagation();
        });

        this._dialog.getElementsByClassName('btn-close')[0].addEventListener('click', () => this._closeDialog());

        if (certs && certs.length > 0) {
            // Ok select
            this._dialog.getElementsByClassName('btn-ok')[0].addEventListener('click', () => {
                if (this._certEl) {
                    let index = Number(this._certEl.getAttribute('index'));
                    if (this._certs[index]) {
                        this._certCallback(this._certs[index]);
                    }
                } else {
                    this._certCallback(null);
                }
                this._closeDialog();
            });

            // On click cert item
            for (let i = 0; i < certEl.length; i++) {
                certEl[i].addEventListener('click', (e) => this._onClickCertEl(e));
            }

            // Select first cert
            certEl[0].click();
        }

        // Esc key close dialog
        document.addEventListener('keydown', this._onEscapeKey);

        document.body.appendChild(this._dialog);
    }

    private _buildCertHtml(certs: CertData[]) {
        let html = '';
        certs.forEach((cert: CertData, index) => {
            html += `
            <div class="webtoken-cert__item" index="${index}">
            <div class="webtoken-cert__title">${cert.subjectData.CN}</div>
            <div class="webtoken-cert__text">${this.textIssuer}: ${cert.issuerData.CN}</div>
            <div class="webtoken-cert__text">${this.textValid}: ${this.dateDisplay(cert.validFrom)} - ${this.dateDisplay(cert.validTo)}</div>
            </div>
            `;
        });
        return html;
    }

    private _onClickCertEl(e) {
        let certEl = this._findCerEl(e.target);
        if (certEl.classList.contains('active')) {
            return;
        }
        if (this._certEl) {
            this._certEl.classList.remove('active');
        }
        certEl.classList.add('active');
        this._certEl = certEl;
    }

    private _findCerEl(certEl) {
        if (!certEl.classList.contains('webtoken-cert__item')) {
            return this._findCerEl(certEl.parentNode);
        }
        return certEl;
    }

    private _closeDialog() {
        document.body.removeChild(this._dialog);
        document.removeEventListener('keydown', this._onEscapeKey);
        this._dialog = null;
        this._certEl = null;
        this._certCallback = null;
    }

    private _onEscapeKey(e) {
        if (e.which === 27) {
            this._closeDialog();
        }
    }
}
