export class DialogAction {
    title: string;
    iconCls: string;
    cls: string;
    fn?: Function;

    constructor(title: string, cls: string, iconCls: string, fn?: Function) {
        this.title = title;
        this.cls = cls;
        this.iconCls = iconCls;
        if (fn) {
            this.fn = fn;
        }
    }
}
