import {
    Component, ElementRef, OnDestroy, OnInit, SimpleChanges
} from '@angular/core';
import {AbstractSideNavComponent} from "../abstract-side-nav.component";
import {Subscription} from "rxjs";
import {SystemApplication} from "../../../model";
import {Router} from "@angular/router";
import {CoreService} from "inet-core";

declare let iNet: any;

@Component({
    selector: 'app-admin-app-side-nav',
    templateUrl: './app-side-nav.component.html',
    styles: [`
        iframe {
            width: 100%;
            border: none;
            overflow: hidden;
        }
    `]
})
export class AppSideNavComponent extends AbstractSideNavComponent implements OnInit, OnDestroy {
    //private loaded = false;
    private _router: Subscription;
    applications: Array<SystemApplication> = [];
    widgetApps : Array<SystemApplication> = [];
    systemUrl: string = 'about:blank';
    SYSTEM_APPS = ['social', 'calendar'];

    constructor(
        private coreService: CoreService,
        private router: Router,
        public elementRef: ElementRef) {
        super(elementRef);

        this.listApp();
    }

    ngOnInit() {
        window.addEventListener('message', (e) => {
            var __data = e.data || {};
            var __eventName = __data['eventName'];
            var __result = {};
            if (!iNet.isEmpty(__data.result)) {
                if (typeof (__data.result) === 'string') {
                    __result = JSON.parse(__data.result);
                } else if (typeof (__data.result) === 'object') {
                    __result = __data.result || {};
                }
                //console.log('[message]', __result);
                switch (__eventName) {
                    case 'open':
                    case 'openApp':
                        this.openApp(__result['application']);
                        this.close();
                        break;
                    case 'openUrl':
                        window.location.href = iNet.getSSOUrl(__result['url']);
                        break;
                    case 'loaded':
                        const iFrame = document.querySelectorAll(`iframe[src="${__result['src']}"]`)[0];
                        if(iFrame) {
                            iFrame['height']=__result['height'];
                            iFrame['valid'] = true;
                        }
                        break;
                }
            }

        }, false);
    }

    ngOnDestroy(): void {
        this.close();
        if (this._router) {
            this._router.unsubscribe();
        }
    }

    ngOnChanges(changes: SimpleChanges) {
        const currentValue = changes['opened']["currentValue"];
        setTimeout(() => {
            if (currentValue) {
                this.systemUrl = iNet.getPUrl('common/page/apps') + '?appname=system';
                this.open();
            } else {
                this.close();
            }
        }, 250)
    }

    private listApp(){
        this.coreService.getSystemApplication().subscribe((apps: Array<SystemApplication>) => {
            this.applications = apps
                .filter((app: SystemApplication) => (app.organId !== 'community' && app.firmContext))
                .map((application) => {
                    if (application.homepage.indexOf('http') < 0) {
                        application.homepage = iNet.getPUrl(`${application.module}/${application.homepage}`);
                        application.icon = `${this.coreService.getFileServerPath()}/images/${application.module}/${application.icon}`;
                    }
                    if(!!application.widget) {
                        application.widget = `${iNet.getPUrl('common/page/apps')}?appname=${application.firmContext}`; //rebuild the widget url
                    }
                    return application;
                });
            this.widgetApps = this.applications.filter((app: SystemApplication) => (!!app.widget));
        });
    }

    openApp(app: string) {
        if (this.SYSTEM_APPS.includes(app)) { //system app
            this.router.navigate([app]);
        } else {
            const url = iNet.getSSOUrl(this.getUrlByContext(app));
            if (url) {
                window.location.href = url;
            }
        }
    }

    private getUrlByContext(content: string) {
        const app = this.applications.find(({webContext}) => webContext === content);
        return app ? app.homepage : null;
    }
}
