import {Directive, HostListener, Input} from '@angular/core';
import {ViewerService} from './viewer.service';
import {NavigationExtras} from "@angular/router";

@Directive({
    selector: '[appViewer]'
})
export class ViewerDirective {

    @Input('ext') ext: string;
    @Input('docId') docId: string;
    @Input('files') files?: Array<any>;
    @Input('extras') extras?: NavigationExtras;
    constructor(private viewerService: ViewerService) {
    }

    @HostListener('click') onClick() {
        this.viewerService.setFiles(this.files);
        this.viewerService.open(this.ext, this.docId, this.extras);
    }

}
