import {Component, Input, OnInit, Output, EventEmitter, OnDestroy} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {of, Subject, Subscription} from "rxjs";
import {ErrorMessage, NotificationService} from "inet-core";
import {EmailTemplateService} from "../email-template.service";
import {TranslateService} from "@ngx-translate/core";
import {EmailTemplate} from "../email-template";
@Component({
    selector: 'app-email-template-edit',
    templateUrl: './email-template-edit.component.html',
    styleUrls: ['./email-template-edit.component.css']
})
export class EmailTemplateEditComponent implements OnInit, OnDestroy {
    email = new EmailTemplate();
    emailTemplateForm: FormGroup;
    @Input('visibleProperty') visibleProperty: boolean = false; //False to hide the Property Panel.
    @Input() emailId: string;
    emailParams = [];
    editorConfig = {
        "editable": true,
        "spellcheck": true,
        "height": "calc(100vh - 205px)",
        "minHeight": "100%",
        "width": "100%",
        "minWidth": "0",
        "translate": "yes",
        "enableToolbar": true,
        "showToolbar": true,
        "placeholder": "",
        "imageEndPoint": "",
        "toolbar": [
            ["bold", "italic", "underline", "strikeThrough"],
            ["fontName", "fontSize", "color"],
            ["justifyLeft", "justifyCenter", "justifyRight", "justifyFull", "indent", "outdent"],
            ["emailParam"]
        ]
    };
    @Output() onValidate = new EventEmitter<boolean>();
    @Output() onUpdate = new EventEmitter<any>();
    @Output() onToggleProperty = new EventEmitter<boolean>();
    private formChanged: Subject<boolean> = new Subject<boolean>();


    private translateSubscription: Subscription;
    private columnsTranslations: any;

    constructor(private fb: FormBuilder,
                private notification: NotificationService,
                private translate: TranslateService,
                private emailTemplateService: EmailTemplateService) {

        this.translateSubscription = translate.get(['COMMON.MODULE.EMAIL_TEMPLATE']).subscribe(data => {
            this.columnsTranslations = data['COMMON.MODULE.EMAIL_TEMPLATE'];
        });
    }

    ngOnInit() {
        this.initForm();
    }

    ngOnDestroy() {
        this.translateSubscription.unsubscribe();
    }

    initForm() {
        this.emailTemplateForm = this.fb.group({
            templateId: [null],
            emailCode: [null, Validators.required],
            emailName: [null, Validators.required],
            emailSubject: [null],
            emailContent: [null],
            inactive: [null]
        });

        this.formChanged.debounceTime(250).distinctUntilChanged().subscribe(v => {
            this.onValidate.emit(v);
        });
        this.emailTemplateForm.valueChanges.subscribe((v: EmailTemplate) => {
            this.formChanged.next(this.emailTemplateForm.valid);
        });
    }

    loadById(emailId: string) {
        if (emailId) {
            this.emailTemplateService.loadById(emailId)
                .switchMap(data => {
                    if (data.type !== ErrorMessage.TYPE) {
                        this.setData({...this.email, ...data});
                        return this.emailTemplateService.listParams(this.email.application, this.email.srvVariable);
                    } else {
                        return of(null);
                    }
                })
                .subscribe(result => {
                    this.emailParams = result.items || [];
                });
        }
    }

    toggleProperty(): boolean {
        this.visibleProperty = !this.visibleProperty;
        this.onToggleProperty.emit(this.visibleProperty);
        return this.visibleProperty;
    }

    setData(emailTemplate: EmailTemplate) {
        this.email= emailTemplate;
        this.email.templateId = this.email.uuid;
        this.email.inactive = !this.email.inactive;//active
        this.emailTemplateForm.patchValue(emailTemplate);
    }

    clearData(){
        this.emailTemplateForm.reset();
    }

    setEditorPlaceholder(placeholderContent: string){
        this.editorConfig.placeholder = placeholderContent;
    }

    private getData(): any {
        const data = this.emailTemplateForm.getRawValue();
        data.inactive = !data.inactive;//active
        return data;
    }

    update() {
        if (this.emailTemplateForm.valid && this.email && this.email.uuid) {
            const {UPDATE_SUCCESS, UPDATE_ERROR, EMAIL} = this.columnsTranslations;
            this.emailTemplateService.update(this.getData()).subscribe(data => {
                if (data.type !== ErrorMessage.TYPE) {
                    this.setData({...this.email, ...data});
                    this.notification.showMessage(UPDATE_SUCCESS, 'success', EMAIL);
                } else {
                    this.notification.showMessage(UPDATE_ERROR, 'error', EMAIL);
                }
                this.onUpdate.emit(data)
            });
        }
    }
}
