import {Component, Input, TemplateRef, ViewChild} from '@angular/core';
import {BsModalRef, BsModalService} from "ngx-bootstrap";
import {CoreService} from "inet-core";

@Component({
  selector: 'app-modal-select',
  templateUrl: './modal-select.component.html',
  styleUrls: ['./modal-select.component.css']

})
export class ModalSelectComponent{
  @Input('selectUsers') selectUsers: any[] = [];
  @Input('isSingle') isSingle: boolean;

  @ViewChild('selectModal') selectModal: TemplateRef<any>;

  modalRef: BsModalRef;
  users: any[] = [];
  key: string;
  private timer: any;

  constructor( private coreService: CoreService,
               private modalService: BsModalService) { }

  setDefault() {
    this.selectUsers = [];
  }

  searchUser() {
    clearTimeout(this.timer);
    let value = this.key || '';
    this.timer = setTimeout(() => {
      this.coreService.searchFirmAccount({keyword: value, pageSize: 10}, (data) => {
        let users = data && data['items'] || [];
        this.setUsers(users);
      });
    },300);
  }

  addUser(item: any){
    if(this.isSingle){
      if(this.selectUsers[0]){
        this.users.push(this.selectUsers[0]);
      }
      this.selectUsers[0] = item;
    }
    else{
      for(let i = 0; i < this.selectUsers.length; i++){
        if(this.selectUsers[i].username === item.username){
          return;
        }
      }
      this.selectUsers.push(item);
    }
    this.setUsers(this.users);
  }

  removeUser(item: any){
    for(let i = 0; i < this.selectUsers.length; i++){
      if(this.selectUsers[i].username === item.username){
        this.users.push(item);
        this.selectUsers.splice(i, 1);
        return;
      }
    }
  }

  show(){
    this.searchUser();
    this.modalRef = this.modalService.show(this.selectModal);
  }

  hide(){
    this.modalRef.hide();
  }

  private hasUser(user): boolean {
    for (let item of this.selectUsers) {
      if (item.username === user.username) {
        return true;
      }
    }
    return false;
  }

  private setUsers(users: any[]) {
    this.users = users.filter((item) => !this.hasUser(item));
  }
}
