import {
    Component,
    ElementRef,
    EventEmitter,
    HostListener,
    Input,
    OnChanges, OnDestroy,
    Output,
    SimpleChanges
} from "@angular/core";

@Component({
    selector: 'app-admin-side-nav',
    template: ``
})

export class AbstractSideNavComponent implements OnChanges, OnDestroy {
    @Input() opened: boolean = false;
    @Output() onClose = new EventEmitter<boolean>();
    @Output() onClear = new EventEmitter<boolean>();
    @Output() onLoad = new EventEmitter<boolean>();
    private overflowCls = 'overflow-hidden';
    constructor(public elementRef: ElementRef) { }

    ngOnChanges(changes: SimpleChanges) {
        const currentValue = changes['opened']["currentValue"];
        setTimeout(() => {
            if (currentValue) {
                this.open();
            } else if (changes['opened']['previousValue'] != currentValue) {
                this.close();
            }
        }, 1)
    }

    open() {
        if(!document.body.classList.contains(this.overflowCls)) {
            document.body.classList.add(this.overflowCls);
        }
    }

    close() {
        if(document.body.classList.contains(this.overflowCls)) {
            document.body.classList.remove(this.overflowCls);
        }
        this.opened = false;
        this.onClose.emit(this.opened);
    }

    ngOnDestroy(): void {
    }

    @HostListener('document:click', ['$event'])
    clickOut(event) {
        if (!this.elementRef.nativeElement.contains(event.target)
            && this.opened && !event.target.closest('.nav-item') &&
            !event.target.closest('.search-input__icon')) {
            this.close();
        }
    }
    @HostListener('click',  ['$event'])
    clickInside(event) {
        if(this.elementRef.nativeElement.contains(event.target)) {

        }
    }
}
