import {Component, Input, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {BsModalService} from 'ngx-bootstrap/modal';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {DialogAction} from "../dialog-action";
import {Subscription} from "rxjs";
import {TranslateService} from "@ngx-translate/core";

declare let iNet: any;

@Component({
    selector: 'app-confirm-dialog',
    templateUrl: './confirm-dialog.component.html'
})
export class ConfirmDialogComponent implements OnInit {

    @ViewChild('confirmModal') confirmModal: TemplateRef<any>;

    @Input('id') id: string = iNet.generateId();
    @Input('iconCls') iconCls: string = '';
    @Input('title') title: string = '';
    @Input('content') content: string = '';
    @Input('cls') cls: string = '';
    @Input('actions') actions: Array<DialogAction> = [];

    private modalRef: BsModalRef;
    private data: any;
    private toolbarTranslations: any;
    private readonly translateSubscription: Subscription;

    constructor(private modalService: BsModalService, private translate: TranslateService) {
        this.translateSubscription = translate.get(['TOOLBAR']).subscribe(res => {
            const {TOOLBAR} = res || {'TOOLBAR': {'CLOSE': 'Close'}};
            this.toolbarTranslations = TOOLBAR;
        });
    }

    ngOnInit() {
        if (this.getActions().length < 1) {
            const {CLOSE} = this.toolbarTranslations;
            let closeAction = new DialogAction(CLOSE, 'btn-danger', 'fa fa-check', this.hide);
            this.setActions([closeAction]);
        }
    }

    public hide() {
        this.modalRef.hide();
    }

    show() {
        this.modalRef = this.modalService.show(this.confirmModal);
    }

    pushAction(action: DialogAction) {
        this.actions.push(action)
    }

    setActions(actions: Array<DialogAction>) {
        this.actions = actions;
    }

    getActions(): Array<DialogAction> {
        return this.actions;
    }

    setData(v: any) {
        this.data = v;
    }

    getData(): any {
        return this.data;
    }

    getId(): string {
        return this.id;
    }
}
