import {OnInit} from '@angular/core';
import {ElementRef} from '@angular/core';
import {Input, Output} from '@angular/core';
import {EventEmitter} from '@angular/core';
import {OnChanges, SimpleChanges} from '@angular/core';
import {Directive} from '@angular/core';

declare let $: any;

@Directive({
    selector: 'select[appSelect2]'
})
export class AppSelect2Directive implements OnInit, OnChanges {
    @Input() initValue: string;
    @Input() options: any;
    @Output() onChange = new EventEmitter<any>();

    element: any;

    constructor(
        private _el: ElementRef
    ) {
    }

    ngOnInit() {
        this.element = $(this._el.nativeElement).select2(this.options).change((e) => {
            this.onChange.emit(e.target.value);
        });
        this.updateSelect();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.options) {
            this.updateSelect();
        }
        if (changes.initValue) {
            this.updateValue();
        }
    }

    updateSelect() {
        if (this.element && this.options) {
            this.element.empty().select2(this.options);
            this.updateValue();
        }
    }

    updateValue() {
        if (this.element && this.initValue) {
            this.element.val(this.initValue).trigger('change');
        }
    }

}
