import {Component, EventEmitter, OnInit, Output, TemplateRef, ViewChild} from '@angular/core';
import {BsModalService} from 'ngx-bootstrap/modal';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {ImageCroppedEvent} from "../image-cropper.component";
@Component({
    selector: 'app-cropper-avatar-dialog',
    templateUrl: './cropper-avatar-dialog.component.html'
})
export class CropperAvatarDialogComponent implements OnInit {

    @ViewChild('cropperAvatarModal') cropperModal: TemplateRef<any>;

    private modalRef: BsModalRef;

    cropperReady = false;
    imageChangedEvent: any;
    imageCroppedEvent: ImageCroppedEvent;
    @Output() onCropped = new EventEmitter();
    @Output() onHide = new EventEmitter();
    @Output() onSelectFile = new EventEmitter();
    private isModalShown = false;
    constructor(private modalService: BsModalService) {}

    ngOnInit() {
        this.modalService.onShow.subscribe(() => {
            this.isModalShown = true;
        });

        this.modalService.onHide.subscribe(() => {
            this.isModalShown = false;
        });
    }

    imageCropped(event: ImageCroppedEvent) {
        this.imageCroppedEvent = event;
    }

    imageLoaded() {
        this.cropperReady = true;
    }

    loadImageFailed() {
        //console.log('Load failed');
    }

    setImageChangedEvent($event: any) {
        this.imageChangedEvent = $event;
    }

    hide() {
        this.modalRef.hide();
        this.setImageChangedEvent({});
        this.onHide.emit(this.imageCroppedEvent.base64);
    }

    show($event) {
        this.setImageChangedEvent($event);
        if(!this.isModalShown) {
            this.modalRef = this.modalService.show(this.cropperModal);
        }
    }

    select($event) {
        this.onSelectFile.emit($event);
    }

    submit() {
        this.onCropped.emit(this.imageCroppedEvent.base64);
        this.hide();
    }

    getImageBlob(): Blob {
        return this.imageCroppedEvent.file;
    }

}

