import {Directive, ElementRef, EventEmitter, OnDestroy, OnInit, Output} from '@angular/core';
declare let $: any;

@Directive({
  selector: '[appFrontView]',
  exportAs: 'appFrontView'
})
export class FrontViewDirective implements OnInit, OnDestroy {
  @Output() onShow = new EventEmitter<any>();
  @Output() onHide = new EventEmitter<any>();

  isShow: boolean = false;

  private _$el;
  constructor(
    public _el: ElementRef
  ) { }

  ngOnInit() {
    this._$el = $(this._el.nativeElement);
    this._$el.click((e) => {
      if (e.target.isSameNode(this._el.nativeElement)) {
        this.hide();
      }
    });
    document.body.appendChild(this._el.nativeElement);
  }

  ngOnDestroy() {
    this._el.nativeElement.remove();
  }

  show() {
    this.isShow = true;
    this._$el.show();
    this.onShow.emit();
  }

  hide() {
    this.isShow = false;
    this._$el.hide();
    this.onHide.emit();
  }
}
