import {Component, OnInit} from '@angular/core';
import {GlobalContactService} from "../global-contact.service";
import {ErrorMessage} from "inet-core";

@Component({
    selector: 'app-access-role',
    templateUrl: './access-role.component.html'
})
export class AccessRoleComponent implements OnInit {

    arrayRoleUser: Array<any> = [];
    constructor(private glService: GlobalContactService) {
    }

    ngOnInit() {
        this.loadAccessRole();
    }

    loadAccessRole() {
        this.glService.getAccessRoles().subscribe((result: any) => {
           if (result.type !== ErrorMessage.TYPE) {
               this.arrayRoleUser = result.items;
           }
        });
    }

}
