import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {Observable, of, Subject} from "rxjs";
import {SharingInformationService} from "../sharing-information.service";
import {NotificationService} from "inet-core";

declare let iNet: any;

@Component({
    selector: 'app-sharing-information-template',
    templateUrl: './sharing-information-template.component.html',
    styleUrls: ['./sharing-information-template.component.scss']
})
export class SharingInformationTemplateComponent implements OnInit {
    total: number;
    page = 0;
    limit = 10;
    keyword: string;
    finishLoading: boolean = false;

    subItem: any;
    dataTables: any[] = [];
    searchData: any[] = [];
    dataTableInfo: any;

    searchTerm$ = new Subject<string>();
    destroy$: Subject<boolean> = new Subject<boolean>();

    private __userOnAdd: any;
    @ViewChild('suggestEle') suggestElement: ElementRef;

    constructor(private sharingInformationService: SharingInformationService,
                private notification: NotificationService) {
    }

    ngOnInit() {
        this.onSearchUsers();
        this.loadTableData();
        this.onDocumentClick = this.onDocumentClick.bind(this);
        document.addEventListener('click', this.onDocumentClick);
    }

    ngOnDestroy() {
        document.removeEventListener('click', this.onDocumentClick);
    }

    mapApplicationData(val: any) {
        if(val.organId !== 'community' && val.pattern !== 'calista' && val.firmContext &&  val.pattern){
            return {
                "appName": val.firmContext,
                "appIcon": val.icon
            }
        }
    }

    loadTableData() {
        this.sharingInformationService.getApplicationList({}).concatMap(data => {
            if (data && data.elements) {
                return of(data.elements.map(item => this.mapApplicationData(item)).filter(item=>item))
            }
            return of(null)
        })
            .concatMap((appInfo) => {
                this.dataTableInfo = appInfo || [];
                let appNames = appInfo.map(item => item.appName);
                if (appNames.length) {
                    let __sources = appNames.map((appname, index) =>
                        this.sharingInformationService.getApplicationRights({appname: appname}));
                    return Observable.concat(...__sources)
                }
                return of(null)
            })
            .concatMap((data) => {
                if (data && data.length) {
                    this.dataTables.push(data);
                    return this.sharingInformationService.getUserRights({appname: data[0].application});
                }else{
                    this.dataTableInfo=this.dataTableInfo.filter(item=>item.appName !== data.appname)
                }
                return of(null)
            })
            .subscribe(data => {
                if (data && data.total) {
                    let __userData: any[] = data.items || [];
                    this.dataTables.forEach((items: any[]) => {
                        items.forEach(table => {
                            table.onSearch = false;
                            table.hidden = false;
                            let __users = __userData.filter(__user => __user.group == table.category && __user.application == table.application)
                            if (__users.length && __users[0].application == table.application) {
                                table.data = __users || [];
                                table.data = this.mergeValueInArray(table.data);
                            }
                        })
                    })
                }
            }, () => {

            }, () => {
                this.finishLoading = true;
                // console.log(this.dataTables);
            })
    }

    onToggleTable(subItem: any) {
        if (iNet.isEmpty(subItem.hidden)) {
            subItem.hidden = false;
        } else {
            subItem.hidden = !subItem.hidden
        }
    }

    onSetSubItem(subItem: any) {
        this.subItem = subItem;
    }

    mergeValueInArray(arr: any[]) {
        arr.forEach((item) => {
            // console.log(arr);
            let __users: any[] = arr.filter(data => data.member === item.member) || [];
            if (__users.length > 1) {
                arr = arr.filter(val => val.member !== item.member);
                let __right: string = __users.map(user => user.right).join(",");
                let __uuids: any[] = __users.map(user => [user.right, user.uuid]);
                // let __user: any={};
                arr.push({...__users[0], right: __right, uuids: __uuids});
                // console.log(arr);

            }
            arr.forEach(item => {
                if (!item.uuids) {
                    item.uuids = [[item.right, item.uuid]];
                }
            });
        });
        return arr;
    }

    onCheckUserRight(user: any, subItem: any, $event: any) {
        let __checked = $event.target.checked;
        let __value = $event.target.value;
        if (__checked) {
            this.sharingInformationService.updateUserRight({
                appname: user.application,
                member: user.member,
                right: __value,
                group: user.group,
                // uuid: user.uuid
            }).subscribe(data => {
                if (data && data.member) {
                    subItem.data.push(data);
                    let __users: any[] = subItem.data.filter(item => item.member === data.member && item.uuid) || [];
                    if (__users.length > 1) {
                        let __right: string = __users.map(user => user.right).join(",");
                        subItem.data.forEach(item => {
                            if (item.member == data.member && item.uuids) {
                                item.right = __right;
                                item.uuids.push([__value, data.uuid])
                            }
                        });
                    }
                    if (__users.length === 1) {
                        let __right: string = __users.map(user => user.right).join(",");
                        let __uuids: any[] = __users.map(user => [user.right, user.uuid]);
                        subItem.data.push({...__users[0], right: __right, uuids: __uuids});
                    }
                    subItem.data = subItem.data.filter(item=>item.uuids);
                    // console.log("----->", subItem.data);
                    this.notification.showMessage("ÄÃ£ chia sáº» dá»¯ liá»u cho " + data.member, "success", "Chia sáº» dá»¯ liá»u")
                } else {
                    this.notification.showMessage("CÃ³ lá»i xáº£y ra khi chia sáº» dá»¯ liá»u cho " + user.member, "error", "Chia sáº» dá»¯ liá»u")
                }
            })
        } else {
            let __item: any[] = user.uuids.filter(item => item[0] == __value)[0] || [];
            this.sharingInformationService.deleteUserRight({
                appname: user.application,
                uuid: __item[1] || user.uuid
            }).subscribe(data => {
                if (data && data.uuid) {
                    let __index = user.uuids.findIndex(item => item[1] == data.uuid);
                    user.uuids.splice(__index, 1);
                    let __rights = user.right.split(",").filter(name => name !== __value);
                    user.right = __rights.join(",");
                    if (!user.uuids.length) {
                        // console.log("innnn");
                        let index = subItem.data.findIndex(item => item.member === user.member);
                        index > -1 ? subItem.data.splice(index, 1) : '';
                    }
                    this.notification.showMessage("ÄÃ£ há»§y chia sáº» dá»¯ liá»u cho " + user.member, "success", "Chia sáº» dá»¯ liá»u")
                } else {
                    this.notification.showMessage("CÃ³ lá»i xáº£y ra khi chia dá»¯ liá»u cho " + user.member, "error", "Chia sáº» dá»¯ liá»u")
                }
            })
        }
    }


    onKeyUp($event: any, subItem: any) {
        let __keyword = $event.target.value || "";
        this.searchTerm$.next(__keyword);
    }

    onSearchUsers() {
        this.searchTerm$
            .delay(50)
            .debounceTime(200)
            .switchMap((keyword: string) => {
                this.keyword = keyword;
                return keyword ? this.sharingInformationService.getSuggestUser({keyword: keyword}) : of(null)
            })
            .subscribe((data: any) => {
                let __data: any[] = data ? data.items || [] : [];
                if (__data.length) {
                    __data = __data.filter(item => item.username !== iNet.username);
                }
                this.searchData = __data;
                if (this.subItem.data) {
                    let __temp: any[] = this.subItem.data.map(item => item.member);
                    this.searchData = this.searchData.filter(data => !__temp.includes(data.username))
                }
            })
    }

    onChooseUser(subItem: any, user: any) {
        this.keyword = '';
        this.searchTerm$.next("");
        this.searchData = [];
        let __user: any = {
            application: subItem.application,
            member: user.username,
            right: "",
            group: subItem.category,
            // uuid: iNet.generateId()
        };
        this.__userOnAdd = __user;
        if (!subItem.data) {
            subItem.data = [];
        }
        subItem.data.push(__user);
    }

    onFocusSearchOut(subItem: any) {
        setTimeout(() => {
            this.searchData = [];
            subItem.onSearch = false;
        }, 300)
    }

    onDocumentClick(event: MouseEvent) {
        if (this.suggestElement && this.suggestElement.nativeElement.contains(event.target)) {
            return;
        }
        this.searchData = [];
    }

    scrollMouse($event: any) {
        const target = $event.target;
        if (this.searchData.length !== this.total && (target.offsetHeight + target.scrollTop >= target.scrollHeight)) {
            this.page++;
            this.sharingInformationService.getSuggestUser({
                pageSize: this.limit,
                pageNumber: this.page,
                keyword: this.keyword
            }).subscribe((data) => {
                let newData = data.items || [];
                this.searchData = this.searchData.concat(newData);
            });
        }
    }
}
