import {
    Component, SimpleChanges, ElementRef
} from '@angular/core';
import {NotifyMessageService} from "../../../common/notify-message.service";
import {ResponseData} from "inet-core";
import {NotifyMessage} from "../../../model/notify-message";
import {AbstractSideNavComponent} from "../abstract-side-nav.component";

@Component({
    selector: 'app-admin-message-side-nav',
    templateUrl: './message-side-nav.component.html',
    styleUrls: ['./message-side-nav.component.css']
})
export class MessageSideNavComponent  extends AbstractSideNavComponent   {
    keyword: string;
    loaded = false;
    messages: NotifyMessage[] = [];
    totalMessage: number;
    pageNumber= 0;
    pageSize = 10;
    constructor(private service: NotifyMessageService, public elementRef: ElementRef) {
        super(elementRef);
    }

    ngOnChanges(changes: SimpleChanges) {
        const currentValue = changes['opened']["currentValue"];
        setTimeout(() => {
            if (currentValue) {
                this.open();
                if (!this.loaded) {
                    this.pageNumber = 0;
                    this.listMessage({pageSize: this.pageSize, pageNumber: this.pageNumber, notifylist: true});
                }
            } else {
                this.close();
            }
        }, 1)

    }

    listMessage(params: any) {
        this.service.getMessages(params).subscribe( (response: ResponseData) => {
            this.messages = response.items as Array<NotifyMessage>;
            this.totalMessage = response.total;
            this.loaded= true;
        });
    }

    loadMessageMore() {
        this.pageNumber+=1;
        this.service.getMessages({pageSize: this.pageSize, pageNumber: this.pageNumber, notifylist: true}).subscribe( (response: ResponseData) => {
            this.messages = [...this.messages, ...response.items as Array<NotifyMessage>];
        });
    }

    clearMessage(){
        this.service.clearAll().subscribe(response =>{
            this.messages = [];
            this.totalMessage= 0;
            this.onClear.emit(true);
        })
    }

    openMessage(msg: NotifyMessage) {
        if(!msg.read) {
            this.service.loadNotify(msg.application, msg.activityID).subscribe((message: NotifyMessage) =>{
                msg.read = true;
                this.onLoad.emit(true);
            });
        }
    }
}
