import { Injectable } from '@angular/core';
import {WebTokenSigner} from "./lib/WebTokenSigner";
import {CertData} from "./lib/model/CertData";
import {MessageSignHash} from "./lib/model/MessageSignHash";
import {ReplyData} from "./lib/model/ReplyData";
import {TokenSimulator} from "./lib/TokenSimulator";
import {EXT_HASH_ALG} from "./lib/Constants";
import {Observable, Subscriber} from "rxjs";

@Injectable()
export class WebTokenService {

  private _signer = new WebTokenSigner();

  initForDevMode() {
    console.warn("Init WebTokenSigner for dev mode. Not used it in production mode");
    this._signer.appInstalled = true;
    if (!window["_ExtTokenBridge"]) {
      window["_ExtTokenBridge"] = TokenSimulator;
    }
  }

  ready(): Observable<boolean> {
    return Observable.create((subscriber: Subscriber<boolean>) => {
      this._signer.checkReady().then((supported) => subscriber.next(supported));
    });
  }

  selectCert(forceSelect?: boolean): Observable<CertData> {
    return Observable.create((subscriber: Subscriber<CertData>) => {
      this._signer.selectCert(forceSelect).then((cert: CertData) => subscriber.next(cert));
    });
  }

  getCertBySerial(serial: string): Observable<CertData> {
    return Observable.create((subscriber: Subscriber<CertData>) => {
      this.loadCerts().subscribe((certs: CertData[]) => {
        for (let i = 0; i < certs.length; i++) {
          if (certs[i].serial === serial) {
            return subscriber.next(certs[i]);
          }
        }
        subscriber.next(null);
      });
    });
  }

  selectCertUnlock(serial?: string): Observable<CertData> {
    return Observable.create((subscriber) => {
      if (serial) {
        this.getCertBySerial(serial).subscribe((cert: CertData) => this._unlockCert(cert, subscriber));
      } else {
        this.selectCert().subscribe((cert: CertData) => this._unlockCert(cert, subscriber));
      }
    });
  }

  private _unlockCert(cert: CertData, subscriber: Subscriber<CertData>) {
    if (cert) {
      this.unlockCert(cert).subscribe((unlocked: boolean) => {
        cert.unlocked = unlocked;
        subscriber.next(cert);
      });
    } else {
      subscriber.next(cert);
    }
  }

  unlockCert(cert: CertData): Observable<boolean> {
    return Observable.create((subscriber: Subscriber<boolean>) => {
      // Sign fake data to unlock token
      const fakeData: MessageSignHash = {
        hash: "cA/metLrX7NUOfviDVsGYeRxAIo=",
        hashAlg: EXT_HASH_ALG.SHA1,
        serial: cert.serial
      };
      this.signHash(fakeData).subscribe((response: ReplyData) => {
        if (response && response.data && response.data.signature) {
          subscriber.next(true);
        } else {
          subscriber.next(false);
        }
      });
    });
  }

  loadCerts(): Observable<CertData[]> {
    return Observable.create((subscriber) => {
      this._signer.loadCerts().then((data) => subscriber.next(data));
    });
  }

  signHash(data: MessageSignHash): Observable<ReplyData> {
    return Observable.create((subscriber: Subscriber<ReplyData>) => {
      this._signer.signHash(data).then((response) => subscriber.next(response));
    });
  }
}
