import {
    AfterViewInit,
    Component,
    ContentChild,
    ElementRef, EventEmitter,
    Input,
    OnDestroy,
    OnInit,
    Output,
    TemplateRef, ViewChild
} from '@angular/core';
import {NavigationEnd, Router} from "@angular/router";
import {Subscription} from "rxjs/Subscription";
import {AdminNavbarComponent} from "./navbar/navbar.component";

declare let $: any;

@Component({
    selector: 'app-layout',
    templateUrl: './layout.component.html'
})
export class LayoutComponent implements OnInit, OnDestroy, AfterViewInit {
    @Input('theme') themeClass: string = 'theme-silver';
    @ContentChild('menu') menu: TemplateRef<ElementRef>;
    @ContentChild('navbarItem') navbarItemTpl: TemplateRef<ElementRef>;
    @ContentChild('profileItem') profileItemTpl: TemplateRef<ElementRef>;
    @ContentChild('navbarMenu') navbarMenu: TemplateRef<ElementRef>;
    @ContentChild('toolbar') toolbar: TemplateRef<ElementRef>;

    @Input('homeRouterLink') homeRouterLink: string;
    @Input() appName: string;

    @Input() hideToggler: boolean = false;
    @Input() hideTogglerButton: boolean = true;
    @Input() visibleMenu: boolean = true;
    @Input() expandedMenu: boolean = false;
    @Input() expandedSlider: boolean = false;
    @Input() expandedMessageSlider: boolean = false;
    @Input() expandedAppSlider: boolean = false;
    @Input() expandedSocialSlider: boolean = false;
    @Input() expandedChatSlider: boolean = false;
    @Input() hideSearch: boolean = false;
    @Input() hideCompanyMenu: boolean = false;
    @Input() hideProfileMenu: boolean = false;
    @Input() hideBrandName : boolean = true;
    @Input() hideLogo: boolean = false;
    @Input() visibleOverlay: boolean = false;
    @Input() fullScreenLayout: boolean = false;

    @Input() fullLayoutWithUrls: Array<string> = [];
    @Output() routeChange = new EventEmitter();
    @Output() onToggle = new EventEmitter<boolean>();
    @Output() onClickLogo = new EventEmitter<string>();
    @Input() visibleToolbar: boolean = false;

    @Input() visibleChatIcon: boolean = true;
    @Input() visibleMessageIcon: boolean = true;
    @Input() visibleAppIcon: boolean = true;
    @Input() visibleNoteIcon: boolean = false;

    private _router: Subscription;
    @ViewChild(AdminNavbarComponent) navbarComponent: AdminNavbarComponent;
    constructor(private router: Router) {}

    ngOnInit() {
        if (window.localStorage) {
            this.expandedMenu = (window.localStorage.getItem('expandedMenu')==='1');
        }
        // this.fullLayoutWithUrls.push('/user-profile');
        this._router = this.router.events.filter(event => event instanceof NavigationEnd)
            .subscribe((event: NavigationEnd) => {
                const url = event.url;
                this.onChangeUrl(url);
            });
    }

    private onChangeUrl(url: string) {
        if(!this.fullScreenLayout) {
            let __visibleMenu = !!this.menu;
            if (__visibleMenu) {
                for (let i = 0; i < this.fullLayoutWithUrls.length; i++) {
                    if ((url !== '/' && this.fullLayoutWithUrls[i] !== '/' && url.indexOf(this.fullLayoutWithUrls[i]) === 0)
                        || (url === this.fullLayoutWithUrls[i])) {
                        __visibleMenu = false;
                        break;
                    }
                }
            }
            this.visibleMenu =  __visibleMenu;
        } else {
            this.visibleMenu = false;
        }
        this.hideToggler = !this.visibleMenu;
        this.routeChange.emit({
            url: url,
            visibleMenu: this.visibleMenu,
            hideToggler: this.hideToggler,
            expandedMenu: this.expandedMenu,
            layout: this
        });
    }

    ngAfterViewInit() {
        //Using jQuery to handle menu
        $(document).ready(function () {
            const $menu = $('#mainnav-menu');
            const activeCls = 'active';
            const $elements = $menu.find('li');

            $elements.on('hide.bs.collapse', function () {
                $(this).removeClass(activeCls);
            });

            $elements.on('show.bs.collapse', function () {
                const $currentActive = $menu.find('li.active');
                $currentActive.find('a[data-toggle]').trigger("click");
                $(this).addClass(activeCls);
            });
        });
    }

    ngOnDestroy(): void {
        this._router.unsubscribe();
    }

    onToggleMenu($event) {
        this.expandedMenu = !this.expandedMenu;
        window.localStorage.setItem('expandedMenu', this.expandedMenu ? '1' : '0' );
        window.dispatchEvent(new Event('resize'));
        this.onToggle.emit(this.expandedMenu);
    }

    private updateSlider(v: boolean){
        this.expandedSlider = v;
        this.visibleOverlay = v;
    }

    onToggleMessageSlider($event) {
        this.expandedAppSlider= false;
        this.expandedSocialSlider = false;
        this.expandedChatSlider = false;
        this.expandedMessageSlider = ! this.expandedMessageSlider;
        this.updateSlider(this.expandedMessageSlider);
    }

    onToggleAppSlider($event) {
        this.expandedChatSlider = false;
        this.expandedSocialSlider = false;
        this.expandedMessageSlider= false;
        this.expandedAppSlider = ! this.expandedAppSlider;
        this.updateSlider(this.expandedAppSlider);
    }

    onToggleSocialSlider($event) {
        this.expandedAppSlider= false;
        this.expandedChatSlider = false;
        this.expandedMessageSlider= false;
        this.expandedSocialSlider = ! this.expandedSocialSlider;
        this.updateSlider(this.expandedSocialSlider);
    }
    onToggleNoteSlider($event){
        this.expandedAppSlider= false;
        this.expandedSocialSlider = false;
        this.expandedChatSlider = false;
        this.expandedMessageSlider = false;
        this.updateSlider(this.expandedMessageSlider);
        this.router.navigate(['note','list'])
    }

    onCloseSlider($event) {
        this.expandedMessageSlider= false;
        this.expandedAppSlider= false;
        this.expandedChatSlider = false;
        this.expandedSocialSlider = false;
        this.expandedSlider = false;
        this.visibleOverlay = false;
    }

    //====================Message count=================
    onClearMessage($event) {
        if(this.navbarComponent) {
            this.navbarComponent.setMessageCount('');
        }
    }

    onLoadMessage($event) {
        if(this.navbarComponent) {
            this.navbarComponent.countMessage();
        }
    }

    clickLogo($event) {
        this.onClickLogo.emit(this.homeRouterLink);
    }

}
