    static postToExtension(action: EXT_ACTION, data?: any): Promise<ReplyData> {
        const promise = new Promise((resolve) => {
            switch (action) {
                case EXT_ACTION.GET_INFO:
                    resolve({data: {
                        version: '0.0.1',
                        nativeVersion: 1,
                        nativeLatestVersion: 1,
                    }});
                    break;
                case EXT_ACTION.SIGN_HASH:
                    this._sign(data, resolve);
                    break;
                case EXT_ACTION.GET_CERTS:
                    resolve({data: this._certs});
                    break;
            }
        });
        return promise;
    }

    static _sign(data: MessageSignHash, resolve) {
        try {
            const cert = this._getCert(data.serial);
            const response: SignedData = {
                serial: cert.serial,
                cert: cert.cert,
                hashAlg: data.hashAlg || EXT_HASH_ALG.SHA1
            };
            const jsEncrypt = new JSEncrypt(null);
            jsEncrypt.setPrivateKey(cert.pk);

            if (data.hashs) {
                response.signatures = [];
                data.hashs.forEach((hash) => {
                    response.signatures.push(jsEncrypt.sign(hash, (str) => this._base64toHEX(str), data.hashAlg.toLocaleLowerCase()));
                });
            } else {
                response.signature = jsEncrypt.sign(data.hash, (str) => {
                    return this._base64toHEX(str);
                }, data.hashAlg.toLocaleLowerCase());
            }
            resolve(<SignedReplyData>{
                data: response
            });
        } catch (e) {
            resolve(<SignedReplyData>{
                error: "SIGN_ERROR",
                errorMsg: e.message
            });
        }
    }

    static _getCert(serial) {
        for (let i = 0; i < this._certs.length; i++) {
            if (this._certs[i].serial === serial) {
                return this._certs[i];
            }
        }
        return this._certs[0];
    }

    static _base64toHEX(base64) {

        var raw = atob(base64);

        var HEX = '';

        for (let i = 0; i < raw.length; i++ ) {

            var _hex = raw.charCodeAt(i).toString(16);

            HEX += (_hex.length==2?_hex:'0'+_hex);

        }
        return HEX;
    }
